
/*SORTAPP */
.image-list {
  margin-top: 30px;
  display: grid;
  width: 80vw;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: 10px;
  grid-gap: 0px;
}
.image-list img {
  width: 200px;
}
.sorted-area {
  margin-top: 25px;
  display: flex;
  flex-direction: column;
}
.sortedBin {
  margin: 5px;
  width: 18vw;
  height: 28vh;
  color:white;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}
.star{
  width: 24px;
  height: 24px;
}

.vibrate-2 {
	-webkit-animation: vibrate-2 0.7s linear both;
	        animation: vibrate-2 0.7s linear both;
}

